<template>
    <div>
        <button @click="handClick" :class="['wgf-button',type||'defau']">
            <slot></slot>
        </button>
    </div>
</template>

<script>
export default {
    name:'wgfButton',
    props:['type'],
    data () {
        return {
        }
    },
    methods:{
        handClick(event){
            this.$emit('click',event)
        }
    }
}
</script>

<style>
.wgf-button {
    padding: 8px 10px;
    border: none;
    outline: none;
    cursor: pointer;
    color: #fff;
    transition: 0.2s;
    border-radius:3px; 
}

.wgf-button:hover {
    box-shadow: 0 6px 10px #999;
    transform: translateY(-3px)
}

.wgf-button:active {
    transition: none;
    box-shadow: none;
}

.defau{
    background: #ecf5ff;
    border: 1px solid #dcdfe6;
    color: #606266;
}
.primary{
    background: #21b9d3;
}
.success{
    background: #67c23a;
}
.warning{
    background: #e6a23c;
}
.danger{
    background: #f56c6c;
}
</style>
