<template>
    <div
      :style="{width:width+'px',height:height+'px',background:background,color:color,opacity:opacity,left:left,bottom:bottom}"
      id="drag"
      class="wgfFloating" 
      ref="floating_box"
      v-drag="{'screenHeight':screenHeight,'screenWidth':screenWidth,'height':height,'width':width}">
        <slot></slot>
    </div>
</template>
<script>
export default {
  name: "wgfFloating",
  props: {
    width: {
      type: [String, Number],
      default: function() {
        return "60";
      }
    },
    height: {
      type: [String, Number],
      default: function() {
        return "60";
      }
    },
    background: {
      type: String,
      default: function() {
        return "";
      }
    },
    color: {
      type: String,
      default: function() {
        return "#333";
      }
    },
    opacity: {
      type: [String, Number],
      default: function() {
        return "0.8";
      }
    }
  },
  data() {
    return {
      screenWidth:
        document.documentElement.clientWidth || document.body.clientWidth, //屏幕宽度
      screenHeight:
        document.documentElement.clientHeight || document.body.clientHeight, //屏幕高度
      left: (JSON.parse(localStorage.getItem("tip")) || {}).left || "50px",
      bottom: (JSON.parse(localStorage.getItem("tip")) || {}).bottom || "50px"
    };
  },
  created() {},
  //自定义拖拽指令
  directives: {
    drag(el, binding) {
      let oDiv = el; //当前元素
      oDiv.onmousedown = function(e) {
        oDiv.style.transition = "initial";
        oDiv.onmouseleave = null;
        oDiv.onmouseenter = null;
        e.preventDefault();
        //鼠标按下，计算当前元素距离可视区的距离
        let disX = e.clientX - oDiv.offsetLeft;
        let disY = e.clientY - oDiv.offsetTop;
        document.onmousemove = function(e) {
          e.preventDefault();
          //通过事件委托，计算移动的距离
          let l = e.clientX - disX;
          let t = e.clientY - disY;
          //设置边界
          if (l < 0) {
            l = 0;
          }
          if (l > binding.value.screenWidth - binding.value.width) {
            l = binding.value.screenWidth - binding.value.width;
          }
          if (t < 0) {
            t = 0;
          }
          if (t > binding.value.screenHeight - binding.value.height) {
            t = binding.value.screenHeight - binding.value.height;
          }
          //移动当前元素
          oDiv.style.left = l + "px";
          oDiv.style.top = t + "px";
          //将此时的位置传出去
        };
        let H =
          document.documentElement.clientHeight || document.body.clientHeight;
        document.onmouseup = function() {
          document.onmousemove = null;
          document.onmouseup = null;
          let tip = {
            left: el.offsetLeft + "px",
            bottom: H - el.offsetTop - el.offsetHeight + "px"
          };
          window.localStorage.setItem("tip", JSON.stringify(tip));
          let l = oDiv.offsetLeft;
          let t = oDiv.offsetTop;

          if (l == 0) {
            oDiv.style.left = -binding.value.width + 30 + "px";
          } else if (l == binding.value.screenWidth - binding.value.width) {
            oDiv.style.left = binding.value.screenWidth - 30 + "px";
          } else if (t == 0) {
            oDiv.style.top = -binding.value.height + 30 + "px";
          } else if (t == binding.value.screenHeight - binding.value.height) {
            oDiv.style.top = binding.value.screenHeight - 30 + "px";
          }
          oDiv.style.transition = ".5s";

          oDiv.onmouseleave = function() {
            if (l == 0) {
              oDiv.style.left = -binding.value.width + 30 + "px";
            } else if (l == binding.value.screenWidth - binding.value.width) {
              oDiv.style.left = binding.value.screenWidth - 30 + "px";
            } else if (t == 0) {
              oDiv.style.top = -binding.value.height + 30 + "px";
            } else if (t == binding.value.screenHeight - binding.value.height) {
              oDiv.style.top = binding.value.screenHeight - 30 + "px";
            }
            oDiv.style.transition = ".5s";
          };

          oDiv.onmouseenter = function() {
            let l = oDiv.offsetLeft;
            let t = oDiv.offsetTop;
            if (l == -binding.value.width + 30) {
              oDiv.style.left = "0px";
            } else if (l == binding.value.screenWidth - 30) {
              oDiv.style.left =
                binding.value.screenWidth - binding.value.width + "px";
            } else if (t == -binding.value.height + 30) {
              oDiv.style.top = "0px";
            } else if (t == binding.value.screenHeight - 30) {
              oDiv.style.top =
                binding.value.screenHeight - binding.value.height + "px";
            }
          };
        };
      };
    }
  }
};
</script>
<style scoped>
.wgfFloating {
  position: fixed;
  bottom: 50px;
  left: 50px;
  z-index: 999;
  transition: transform 1s;
  cursor: grab;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  background: radial-gradient(circle closest-side at 60% 43%, #b03 26%, rgba(187,0,51,0) 27%),radial-gradient(circle closest-side at 40% 43%, #b03 26%, rgba(187,0,51,0) 27%),radial-gradient(circle closest-side at 40% 22%, #d35 45%, rgba(221,51,85,0) 46%),radial-gradient(circle closest-side at 60% 22%, #d35 45%, rgba(221,51,85,0) 46%),radial-gradient(circle closest-side at 50% 35%, #d35 30%, rgba(221,51,85,0) 31%),radial-gradient(circle closest-side at 60% 43%, #b03 26%, rgba(187,0,51,0) 27%) 50px 50px,radial-gradient(circle closest-side at 40% 43%, #b03 26%, rgba(187,0,51,0) 27%) 50px 50px,radial-gradient(circle closest-side at 40% 22%, #d35 45%, rgba(221,51,85,0) 46%) 50px 50px,radial-gradient(circle closest-side at 60% 22%, #d35 45%, rgba(221,51,85,0) 46%) 50px 50px,radial-gradient(circle closest-side at 50% 35%, #d35 30%, rgba(221,51,85,0) 31%) 50px 50px;
  background-color:#b03;
  background-size:100px 100px;
}
</style>

