<template>
  <div class="dw" ref="gd" @mouseover="hover" @mouseout="up" @click="move">
    <p><slot/></p>
    <p ref="gd2" :style="{color:colors}"><slot/></p>
  </div>
</template>

<script>
export default {
  name:'wgfGotop',
  props:['color'],
  data () {
    return {
      colors:'#cc0001'
    }
  },
  methods: {
    move() {
      let timer = setInterval(() => {
        let moveScrollTop = document.body.scrollTop || document.documentElement.scrollTop;
        let speed = Math.floor(-(moveScrollTop - 0) / 10);
        if (moveScrollTop <= 0) {
          clearInterval(timer);
          timer = null;
          this.up();
        } else {
          document.documentElement.scrollTop = moveScrollTop + speed;
          document.body.scrollTop = moveScrollTop + speed;
          this.$refs.gd2.style.transition = "bottom 0.6s"
          this.$refs.gd2.style.bottom = "1000px"
        }
      }, 10);
    },
    hover() {
      this.$refs.gd2.style.transition = "bottom 0.5s"
      this.$refs.gd2.style.bottom = "40px"
    },
    up() {
      this.$refs.gd2.style.transition = "none"
      this.$refs.gd2.style.bottom = "-60px"
    }
  },
  mounted() {
    let dw = this.$refs.gd;
    window.onscroll = function() {
      this.scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
      if (this.scrollTop > 50) {
        dw.style.right = "20px"
      } else {
        dw.style.right = "-40px"
      }
    };
    this.colors = this.color||'#cc0001';
  }
}
</script>

<style>
.dw {
  width: 42px;
  height: 42px;
  line-height: 42px;
  border-radius: 50%;
  z-index: 666;
  justify-content: center;
  align-content: center;
  position: fixed;
  right: -40px;
  bottom: 40px;
  transition: right 0.5s;
  cursor: pointer;
}

.dw>p {
  margin: 0;
  text-align: center;
  font-size: 40px;
  width: 42px;
  height: 42px;
  color: #666666;
  cursor: pointer;
}

.dw>p:last-child {
  position: fixed;
  width: 42px;
  height: 42px;
  right: 20px;
  bottom: -60px;
}
</style>
