export default {
  bind(el, binding) {
    setTimeout(() => {
      const opts = Object.assign(
        {
          top: 0, // 玻璃块上边距
          left: 0, // 玻璃块左边距
          width: "20%", // 玻璃块宽度
          height: "30%", // 玻璃块高度
          style: "",   // 玻璃块自定义css
          background: "#fff",  // 若父级背景自定义颜色（图片不需要）  则玻璃块需要增加自己的颜色
          borderRadius: "10px",  // 玻璃块 边角圆弧
          blur: 1,  // 玻璃模糊度
          move: false, // 是否可拖拽  默认不可
          childrenStyle: "" // 玻璃内元素自定义css
        },
        binding.value
      );
      if (el) {
        if (
          !window.getComputedStyle(el, null).position ||
          window.getComputedStyle(el, null).position == "static" ||
          window.getComputedStyle(el, null).position == "initial"
        ) {
          el.style.position = "relative";
        }
        el.style.overflow = "hidden";
        let filter = document.createElement("div");
        let newEl = el.cloneNode(true);
        if (newEl.id) newEl.removeAttribute("id");
        // 设置父级css
        filter.className = ".filter";
        filter.style.cssText = `position: absolute; box-shadow:-2px 2px 10px #333333; background: ${opts.background}; border-radius: ${opts.borderRadius}; overflow: hidden; ${opts.style}; top: ${opts.top}; left: ${opts.left}; width:${opts.width}; height:${opts.height}; cursor: grab;`;
        filter.appendChild(newEl);
        el.appendChild(filter);
        // 设置子级css
        newEl.style.cssText = `position: absolute; top: ${-filter.offsetTop}px;left: ${-filter.offsetLeft}px; filter: blur(${opts.blur}px); background: ${window.getComputedStyle(el, null).background}; ${opts.childrenStyle}; width:${el.offsetWidth}px; height:${el.offsetHeight}px;`;

        // 设置 拖拽
        if(opts.move) {
          filter.onmousedown = function(e) {
            e.preventDefault();
            //鼠标按下，计算当前元素距离可视区的距离
            let disX = e.clientX - filter.offsetLeft;
            let disY = e.clientY - filter.offsetTop;
            document.onmousemove = function(e) {
              e.preventDefault();
              //通过事件委托，计算移动的距离
              let l = e.clientX - disX;
              let t = e.clientY - disY;
              
              //移动当前元素
              filter.style.left = l + "px";
              filter.style.top = t + "px";
              //将此时的位置传出去
              newEl.style.cssText = `position: absolute; top: ${-filter.offsetTop}px; filter: blur(${opts.blur}px); background: ${window.getComputedStyle(el, null).background}; left: ${-filter.offsetLeft}px;${opts.childrenStyle}; width:${el.offsetWidth}px; height:${el.offsetHeight}px;`;
            };
            document.onmouseup = function() {
              document.onmousemove = null;
              document.onmouseup = null;
            };
          };
        }
      }
    });
  }
};
