// // export Button;
// // export Gotop;
// export default {Button,Gotop}

import wgfGotop from './wgf-gotop/wgf-gotop.vue'
import wgfButton from './wgf-button/wgf-button.vue'
import wgfFloating from './wgf-floating/wgf-floating.vue';

// 自定义指令
import wgfPolish from './wgf-polish/wgf-polish.js';
import Sticky from './v-sticky/v-sticky.js';
import Progress from './v-progress/v-progress.js';

const components = [
  wgfGotop,
  wgfButton,
  wgfFloating,
];
const directives = [
  {
    name: "wgfPolish",
    directive: wgfPolish
  },
  {
    name: "sticky",
    directive: Sticky
  },
  {
    name: "progress",
    directive: Progress
  }
];

// 添加install方法
const Wgf = {
  install: function (Vue) {
    components.map(component => {
      Vue.component(component.name, component);
    });
    directives.map(directive => {
      Vue.directive(directive.name, directive.directive);
    });
  }
}

// 导出Wgf
export default Wgf
